import { useCallback } from "react";
import * as Y from 'yjs';
import { QuillBinding } from 'y-quill';
import Quill from "quill";
import "quill/dist/quill.snow.css";

export default function Editor(props) {
    let docid = props.docid;

    let ydoc = new Y.Doc();
    let ytext = ydoc.getText('doc-contents');

    let url = "/api/connect/" + docid;
    const source = new EventSource(url);
    source.addEventListener('sync', event => {
        console.log("SYNC");
        ytext.applyDelta(JSON.parse(event.data));
    });
    source.addEventListener('update', event => {
        console.log("UPDATE");
        ytext.applyDelta(JSON.parse(event.data));
    });
    source.addEventListener('error', error => {
        console.log("ERROR");
        console.log(error);
    });
    source.onmessage = (msg) => {
        console.log("MESSAGE");
        console.log(msg);
    };

    const wrapperRef = useCallback(wrapper => {
        wrapper.innerHTML = "";
        const editor = document.createElement("div");
        wrapper.append(editor);
        const q = new Quill(editor, {theme: "snow"});

        q.on('text-change', function(delta, oldDelta, source) {
            if (source === 'user') {
                let postURL = "/api/op/" + docid;
                fetch(postURL, {
                    headers: {"Content-Type": "application/json"},
                    method: 'POST', 
                    mode: 'cors', 
                    body: JSON.stringify(delta.ops)
                });
            }
        });
        const binding = new QuillBinding(ytext, q);
    }, [])
    
    return (
        <div className="App">
            <div id="container" ref={wrapperRef}></div>
        </div>
    );
}